/* Reset default margins and paddings */
body, html {
  margin: 0;
  padding: 0;
  height: 100%; /* Ensure the body and html take up full height */
}

/* Container styling */
.dashboard-container {
  display: flex;
  min-height: 100vh;
  background-color: #f0f4f8; /* Light background */
}

/* Sidebar styling */
.sidebar {
  width: 250px;
  background-color: #333;
  color: white;
  padding: 20px;
  box-sizing: border-box;
}

.sidebar h2 {
  font-size: 20px;
  margin-bottom: 20px;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar ul li {
  padding: 10px;
  cursor: pointer;
  border-bottom: 1px solid #444;
}

.sidebar ul li:hover {
  background-color: #4caf50; /* Green hover effect */
}

/* Main window styling */
.main-window {
  flex: 1;
  padding: 20px;
  overflow: auto; /* Allow scrolling if content overflows */
  box-sizing: border-box;
}

.history-details h2 {
  color: #333;
}

.history-details p {
  margin: 10px 0;
  font-size: 16px;
}

.history-details strong {
  color: #4caf50; /* Green accent */
}

/* Table styling */
.history-details table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.history-details th,
.history-details td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: left;
  vertical-align: top;
}

.history-details th {
  background-color: #f0f0f0;
  width: 25%;
}

.history-details tr:nth-child(even) {
  background-color: #fafafa;
}

/* QA table container */
.qa-table-container {
  max-height: calc(100vh - 250px); /* Adjust based on other content height */
  overflow-y: auto;
  margin-top: 20px;
}

/* QA table styling */
.qa-table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}

.qa-table th,
.qa-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: left;
  vertical-align: top;
  word-wrap: break-word;
}

.qa-table th {
  background-color: #f0f0f0;
  width: 50%; /* Equal width for question and answer columns */
}

.qa-table tr:nth-child(even) {
  background-color: #fafafa;
}

/* Logout button styling (if needed) */
.logout-btn {
  margin-top: 20px;
  padding: 10px;
  width: 100%;
  cursor: pointer;
  background-color: #4caf50;
  color: white;
  border: none;
  text-align: center;
}
